@import "~frontend-scss-mixins/mixins";

@mixin active-button-effect {

  &:after {
    @include pseudo-absolute;
    @include absolute-fill;
    background: var(--light-main-color);
    z-index: -1;
    transform: scale(0.9);
    border-radius: 4px;
  }

  &:active:after {
    transform: scaleX(1.1) scaleY(1.3);
    opacity: 0;
  }

}

@mixin active-disabled-button-effect($border-radius:4px) {

  &:after {
    @include pseudo-absolute;
    @include absolute-fill;
    background: var(--error-color);
    z-index: -1;
    transform: scale(0.9);
    border-radius: #{$border-radius};
  }

  &:active:after {
    transform: scaleX(1.1) scaleY(1.3);
    opacity: 0;
  }
}

@mixin input-placeholder {
  &::-webkit-input-placeholder {
    @content
  }
  &::-moz-placeholder {
    @content
  }
  &:-moz-placeholder {
    @content
  }
  &:-ms-input-placeholder {
    @content
  }
}

@mixin customPreloader($color,$side:40,$speed:3,$z-index:auto) {
  $image-background: 'data:image/svg+xml;base64,';

  position: relative;

  &:before {
    display: block;
    content: '';
    width: #{$side}px;
    height: #{$side}px;
    position: absolute;
    background: $color;
    left: calc(50% - #{$side / 2}px);
    top: calc(50% - #{$side / 2}px);
    -webkit-mask: url('#{$image-background}');
    mask: url('#{$image-background}');
    mask-repeat: no-repeat;
    mask-size: contain;
    mask-position: center center;
    -webkit-mask-repeat: no-repeat;
    -webkit-mask-size: contain;
    -webkit-mask-position: center center;
    z-index: $z-index;

    @keyframes rotate {
      0% {
        transform: rotate(0);
      }
      50% {
        transform: rotate(180deg);
      }
      100% {
        transform: rotate(360deg);
      }
    }

    animation: rotate #{$speed}s linear;
    animation-iteration-count: infinite;

  }
}

@mixin customVerticalScroll() {
  @include vertical-scroll(
                  10,
                  var(--extra-light-natural-color),
                  var(--light-main-color),
                  var(--main-color),
  );

  &::-webkit-scrollbar-thumb {
    cursor: pointer !important;
  }
}

@mixin clear-page {
  .mobile-clear-page & {
    @content
  }
}

@mixin mask-contain-center-custom($important: false) {
  $important-string: '';

  @if $important == true {
    $important-string: '!important';
  }

  mask-size: contain#{$important-string};
  mask-repeat: no-repeat#{$important-string};
  mask-position: center center#{$important-string};
  -webkit-mask-size: contain#{$important-string};
  -webkit-mask-repeat: no-repeat#{$important-string};
  -webkit-mask-position: center center#{$important-string};
}

@mixin font-calibri {
  font-family: var(--font-calibri);
}

@mixin font-manrope {
  font-family: var(--font-manrope);
}

@mixin mask-contain-center($important: false) {
  $important-string: '';

  @if $important == true {
    $important-string: '!important';
  }

  mask-size: contain#{$important-string};
  mask-repeat: no-repeat#{$important-string};
  mask-position: center center#{$important-string};
  -webkit-mask-size: contain#{$important-string};
  -webkit-mask-repeat: no-repeat#{$important-string};
  -webkit-mask-position: center center#{$important-string};
